import './AdminClienteUpdate.css'

import { IonSpinner } from '@ionic/react'
import Cleave from 'cleave.js/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { AdminItemField, Page } from '../../components'
import { useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { cantidad_fmt } from '../../helpers'

const defaultValues = {
  HORA_PROCESO_COMPRAS: '',
  MONTO_MINIMO_DELIVERY_GRATIS: ''
}

export const AdminConfiguracion: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Loading, setLoading] = useState(false)
  const [Datos, setDatos] = useState(defaultValues)

  const apiToaster = useApiToaster()

  const setup = async () => {
    obtenerConfiguraciones()
    setDatos(rootStore.configuracionClaveValor as any)
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerConfiguraciones = () => {
    rootStore.environment.api.configuraciones().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setConfiguraciones(result.configuraciones)
      } else {
        apiToaster(result, '', 'leer configuraciones')
      }
    })
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const guardar = () => {
    setLoading(true)
    rootStore.environment.api.guardarConfiguracion(Datos).then((result: any) => {
      setLoading(false)
      apiToaster(result, 'Configuración actualizada')
    })
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Configuración</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Configuración</li>
              </ol>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-body">
                  <div className={'form-group'}>
                    <label id={'HORA_PROCESO_COMPRAS'}>Hora de proceso de compras mejoradas / mayoristas</label>
                    <Cleave
                      className={'form-control'}
                      options={{ time: true, timePattern: ['h', 'm'] }}
                      value={Datos.HORA_PROCESO_COMPRAS}
                      onChange={(evt) => setDato('HORA_PROCESO_COMPRAS', evt.target.value)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-body">
                  <div className={'form-group'}>
                    <label id={'MONTO_MINIMO_DELIVERY_GRATIS'}>Monto mínimo para delivery gratis</label>
                    <Cleave
                      className={'form-control'}
                      options={{}}
                      value={cantidad_fmt(Datos.MONTO_MINIMO_DELIVERY_GRATIS)}
                      onChange={(evt) => setDato('MONTO_MINIMO_DELIVERY_GRATIS', evt.target.value)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="">
            <div className={'form-group text-right'}>
              <a href={'/admin'} className={'btn btn-light'}>
                Cancelar
              </a>
              <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                {Loading && <IonSpinner name="crescent" />}
                {!Loading && <span>Guardar</span>}
              </button>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminConfiguracion
